.pagination-container {
    display: flex;
    list-style-type: none;
    justify-content: center;
    margin-top: 40px;
    
    .pagination-item {
      padding: 0 12px;
      height: 32px;
      text-align: center;
      margin: auto 4px;
      border: 1px solid var(--gray-elements, #E2E6E9);
      display: flex;
      box-sizing: border-box;
      align-items: center;
      font-size: 14px;
      font-weight: 600;
      width: 32px;

      &:nth-child(2) {
        margin-left: 8px;
      }

      &:nth-last-child(2) {
        margin-right: 8px;
      }
    
      &.dots:hover {
        background-color: transparent;
        cursor: default;
      }
  
      &:hover {
        border: 1px solid #313237;
        cursor: pointer;
      }
  
      &.selected {
        background-color: #313237;
        cursor: pointer;
        color: #fff;
      }
  
      .arrow {
        &::before {
          position: relative;
          content: '';
          display: inline-block;
          width: 0.4em;
          height: 0.4em;
          border-right: 0.12em solid rgba(0, 0, 0, 0.87);
          border-top: 0.12em solid rgba(0, 0, 0, 0.87);
        }
  
        &.left {
          transform: rotate(-135deg) translate(-50%);
        }
  
        &.right {
          transform: rotate(45deg);
        }
      }
  
      &.disabled {
        pointer-events: none;
  
        .arrow::before {
          border-right: 0.12em solid rgba(0, 0, 0, 0.43);
          border-top: 0.12em solid rgba(0, 0, 0, 0.43);
        }
  
        &:hover {
          background-color: transparent;
          cursor: default;
        }
      }
    }
  }